<template>
  <div class="box">
    <p class="font-small t-center">设置分享信息让更多的朋友参与活动</p>
    <div class="share">
      <div class="weixi">
        <img src="static/imgs/manage/editor/weixi.png">
      </div>
      <div class="triangle"></div>
      <div class="share-main">
        <div class="mar-10">
          <el-input size="medium" v-model="shareSet.shareTitle">
            <i slot="suffix" class="pointer el-input__icon ">
              <img style="position: relative;top: -10px;left: -8px;" src="static/imgs/manage/editor/huan.png">
            </i>
          </el-input>
          <div class="share-content">
            <el-input type="textarea" v-model="shareSet.shareDesc" :rows="3" style="font-size:12px;"></el-input>
            <img :src="shareSet.thumbnailUrl">
          </div>
        </div>

        <div class="share-footer">
          <div class="pointer" @click="showShareName" style="margin-left: 20px;">+插入分享人昵称</div>
          <span>|</span>
          <div class="pointer" @click="showShareTimes" style="margin-right: 20px;">+插入访问次数</div>
        </div>
      </div>

    </div>

    <div class="next">
      <router-link to="/nav-new/basic-setting/assistant">
        <img src="static/imgs/manage/editor/next.png">
        <span>下一步</span>
      </router-link>
    </div>
  </div>
</template>

<script type="text/javascript">
export default {
  props: {
    shareSet: {
      type: Object
    }
  },
  data () {
    return {
      isName: '\n##显示分享人昵称##',
      isTimes: '\n##显示分享次数##'
    }
  },
  methods: {
    showShareName () {
      if (this.shareSet.isShowShareName) {
        this.shareSet.isShowShareName = false
        this.shareSet.shareDesc = this.shareSet.shareDesc.replace(
          this.isName,
          ''
        )
        console.log(false)
      } else {
        this.shareSet.isShowShareName = true
        this.shareSet.shareDesc += this.isName
        console.log(true)
      }
    },
    showShareTimes () {
      if (this.shareSet.isShowShareTimes) {
        this.shareSet.isShowShareTimes = false
        this.shareSet.shareDesc = this.shareSet.shareDesc.replace(
          this.isTimes,
          ''
        )
      } else {
        this.shareSet.isShowShareTimes = true
        this.shareSet.shareDesc += this.isTimes
      }
    }
  },
  mounted () {
    if (this.shareSet.isShowShareName) {
      this.shareSet.shareDesc += this.isName
    }

    if (this.shareSet.isShowShareTimes) {
      this.shareSet.shareDesc += this.isTimes
    }
  }
}
</script>

<style type="text/css" src="common/style/module/manage/new-editor.scss" scoped></style>
<style lang="scss" scoped>
.share {
  display: flex;
  margin: 30px 0;

  .triangle {
    width: 12px;
    height: 12px;
    border-left: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    transform: rotate(45deg);
    position: relative;
    top: 15px;
    left: 6px;
    z-index: 2;
    background: #fff;
  }

  .share-main {
    width: 410px;
    border: 1px solid #d9d9d9;
    border-radius: 3px;
    position: relative;
    z-index: 1;
    background: #fff;
    padding-top: 10px;

    .share-content {
      display: flex;
      margin-top: 10px;

      img {
        width: 60px;
        height: 60px;
        margin: 10px;
      }
    }

    .share-footer {
      height: 22px;
      font-size: 12px;
      line-height: 22px;
      display: flex;
      background: #f2f3f3;
      justify-content: space-around;
    }
  }
}
</style>


